@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 30px;
  margin: 40px 0;
  line-height: normal;
  text-align: center;
  font-size: 0;
  @media screen and (min-width: 768px) {
    height: 40px;
    margin: 50px 0 60px;
  }
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 30px;
  height: 30px;
  padding: 7px 0;
  margin-left: 0;
  color: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.333;
  text-decoration: none;
  vertical-align: middle;
  //   border: none;
  //   border-width: 1px 0;
  border-radius: 5px;

  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
    padding: 13px 2px;
  }
}

// .tui-pagination .tui-page-btn .dark-theme {
//   color: #f4f4f4;
// }

.tui-pagination .tui-page-btn:hover {
  background-color: #f4f4f4;
}

// .tui-pagination .dark-theme .tui-page-btn:hover {
//   background-color: #242424;
//   color: #333;
// }

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;
  //   border-color: #ff6b08;
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #ff6b08;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

